* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-header {
  min-height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.buttons-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 50%;
  height: calc(25vh - 100px);
  margin: 0 auto;
}

.playground {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.title {
  color: #fff;
  font-size: 20px;
}

.join-game-form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.message {
  color: #fff;
}
.error {
  color: #ff0033;
}
.check {
  display: inline-block;
  vertical-align: middle;
  margin-left: 12px;
}
.facilitator-check {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
}
.join-game-form input.text {
  display: block;
  margin: 12px 0 0 0;
  padding: 5px;
  width: 100%;
  height: 42.5px;
  outline: none;
  border-radius: 8px;
  border: none;
}

.join-game-wrapper {
  width: 50%;
  margin: 0 auto;
}

button {
  cursor: pointer;
}

button.join{
  height: 42.5px;
  color: #fff;
  box-shadow: none;
  border: none;
  font-size: 18px;
  outline: none;
  margin-top: 12px;
  width: 100%;
  border-radius: 8px;
}


p.player {
  color: green;
}
p.me {
  color: rgb(211, 57, 57);
}
p.player, p.me {
  font-size: 16px;
}
.container.round-info{
  margin-bottom: 80px;
}

.join {
  background-color: rgb(211, 57, 57);
  transition-duration: 0.4s;
}
.join:hover{
  background-color: green;
  transition-duration: 0.4s;
}
ul {
  list-style: none;
}

.play-button {
  width: 30%;
  height: 75px;
  padding: 10px;
  color: #fff;
  font-size: 18px;
  outline: none;
  border: none;
  border-radius: 8px;
}

@media (max-width: 768px) {
  .buttons-wrapper {
    flex-direction: column;
    width: 100%;
    height: calc(40vh - 100px);
  }
  .button {
    width: 70%;
  }
}

.red {
  background-color: rgb(211, 57, 57);
}

.green {
  background-color: green;
}

table.score td,
table.score th {
  width: 75px;
  height: 42.5px;
  border: 1px solid;
}

table.score {
  border: 1px solid;
  border-collapse: collapse;
  color: #fff;
  margin: 0 auto;
}

button.red:hover {
  transition-duration: 0.4s;
  background-color: rgb(231, 120, 120);
}
button.green:hover {
  transition-duration: 0.4s;
  background-color: rgb(101, 218, 101);
}
.loader {
  border: 8px solid #f3f3f3;
  border-radius: 50%;
  border-top: 8px solid #3498db;
  width: 70px;
  height: 70px;
  margin: 10em auto;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}